/* FOR MOBILE PHONES */

#intro {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#name {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  font-size: 8vw;
  margin: 0;
}

#title {
  font-family: "Poppins", sans-serif;
  font-size: 2.8vw;
  margin: 0;
}

.text-container {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.text-container #name {
  color: rgba(225, 225, 225, 0.01);
  background-image: url("/public/images/wallpaper.jpg");
  background-size: 50%;
  background-repeat: repeat;
  -webkit-background-clip: text;
  background-clip: text;
  animation: animate 30s ease-in-out infinite;
}

@keyframes animate {
  0%,
  100% {
    background-position: left top;
  }
  25% {
    background-position: right bottom;
  }
  50% {
    background-position: left bottom;
  }
  75% {
    background-position: right top;
  }
}

